// navigationStartlong long (unload)UNIXPerformanceTiming.fetchStart
// unloadEventStartlong long unloadUNIX, 0.
// unloadEventEndlong long unloadUNIX 0.
// redirectStartlong long HTTPUNIX0.
// redirectEndlong long HTTPHTTPbiteUNIX0.
// fetchStartlong long HTTP(fetch)UNIX
// domainLookupStartlong long UNIX(persistent connection) PerformanceTiming.fetchStart
// domainLookupEndlong long UNIX(persistent connection) PerformanceTiming.fetchStart
// connectStartHTTPUnixpersistent connectionfetchStart
// (secureConnectionStart)undefinedHTTPSSSL HTTPS 0
// connectEndUnixfetchStart
// secureConnectionStartUnix0
// requestStartHTTPUnix
// responseStartUnix
// responseEndUnix
// domLoadingDOMDocument.readyStateloadingreadystatechangeUnix
// domInteractiveDOMDocument.readyStateinteractivereadystatechangeUnix
// domContentLoadedEventStartDOMContentLoadedDOMUnix
// domContentLoadedEventEndUnix
// domCompleteDOMDocument.readyStatecompletereadystatechangeUnix
// loadEventStartloadUnix0
// loadEventEndloadUnix0

// 
// DNS domainLookupEnd - domainLookupStart
// TCP connectEnd - connectStart
// request responseEnd - responseStart
// dom  domComplete - domInteractive
//  responseStart - navigationStart
// domready domContentLoadedEventEnd - navigationStart
// onload loadEventEnd - navigationStart

import { colorLog } from './colorLog';
import { getQueryString } from '../../core/utils';

function toS(D_MS) {
    return Number(D_MS / 1000).toFixed(4); 
}

 export  function monitor() {
    if (window.performance) {
        console.log('performance', performance);
        const { timing } = window.performance;
        const { navigationStart, domainLookupEnd, domainLookupStart, connectEnd, connectStart, responseEnd,
            responseStart, domComplete, domInteractive, 
            domContentLoadedEventEnd, loadEventStart,
         } = timing;
        
         const youzoneStart = Number(getQueryString('upesnTime') + '000');
         const mlog = [
         {
            key: 'DNS',
            value: toS(domainLookupEnd - domainLookupStart)
        }, {
            key: 'TCP',
            value: toS(connectEnd - connectStart)
        }, {
            key: 'request',
            value: toS(responseEnd - responseStart)
        }, {
            key: 'dom',
            value: toS(domComplete - responseEnd)
        }, {
            key: '',
            value: toS(responseStart - navigationStart)
        }, {
            key: 'domready',
            value: toS(domContentLoadedEventEnd - navigationStart)
        }, {
            key: 'onload',
            value: toS(loadEventStart - navigationStart)
        }, 
        {
            key: 'onload',
            value: toS(loadEventStart - youzoneStart)
        },
    ];
        mlog.forEach(item => colorLog(item.key, item.value + ''));
        
    }
    
}