let yonyouJSAPI = {};
const nameList = {
	'hiddenMenu': {}, //
	'getOAuthCode': {}, //appcode
	'QRCodeScan': {}, //
	'chooseMap': {}, //
	'closeCurrentWebview': {}, //webview
	'openNewWebview': {} //web
};

Object.keys(nameList).forEach(fname => {
	yonyouJSAPI[fname] = function (parameters = {}) {
		return new Promise((resolve, reject) => {
			const YonYouJSBridge = window.JSBridge;
			var data = { ...nameList[fname], parameters };
			if (YonYouJSBridge) {
				YonYouJSBridge.do(fname, {
					...data,
					success: (res) => {
						try {
							console.log(`jsapi${fname},`, res);
							resolve(res);
						} catch (err) {
							reject({ message: 'jsapi' });
						}

					},
					fail: error => {
						console.log('jsapi_err', error);
						reject(error);
					}
				});
			} else {
				// reject({message:'jsAPI'})
				console.log('jsAPI');
			}
		});
	};
});



export default yonyouJSAPI;
